import React, {useEffect, useContext} from 'react';
import Context from "../../Context/Context"
import styles from './styles.module.scss';

import MainCard from '../MainCard';

const Main = () => {
	const context = useContext(Context)
	useEffect(() => {
		fetch("https://us-central1-mirum-e30cc.cloudfunctions.net/api/cards")
			.then(el => el.json())
			.then(el => context.update({
				cards: el
			}))
	}, [])
	
	return (
			<main className={ styles.main }>
				<article className={ styles.main_container }>
					{context.state.cards.length > 0 &&
						context.state.cards.map((el, i) => {
							return <MainCard data={el} key={i}/>
						})
					}
				</article>
			</main>
	)
}

export default Main;
